@import "vars";
@import "mixins";
body {
    font-size: 20px;
}

.bgimg-1 {
    min-height: 580px;
    background-image: url('../images/requirements/tinf/1.jpg');
}

.bgimg-2 {
    min-height: 500px;
    background-image: url('../images/requirements/tinf/2.jpg');
}

.bgimg-3 {
    min-height: 400px;
    background-image: url('../images/requirements/tinf/3.jpg')
}

#animation {
    height: 500px;
    svg {
        height: 500px;
        width: 500px;
    }
}

.bmi {
    width: 250px;
    margin: 5px auto;
}

#compute {
    font-size: 25px;
    border-radius: 25px;
    width: 200px;
    height: 50px;
    border: 1.5px solid lightgrey;
    @include box-sizing(border-box);
    padding: 5px;
    margin: 3px auto;
    background: #28fc19;
}

#login {
    .form {
        width: 250px;
        margin: 5px auto;
        height: 280px;
        @include transition(height 1s, opacity 500ms);
        input {
            margin-bottom: 20px;
        }
    }
}

input[type=submit] {
    font-size: 25px;
    border-radius: 25px;
    width: 200px;
    height: 50px;
    border: 1.5px solid lightgrey;
    @include box-sizing(border-box);
    padding: 5px;
    margin: 3px auto;
    background: #28fc19;
}

#error {
    font-family: $arial;
    color: red;
    font-weight: bold;
    font-size: 20px;
    display: none;
}

#success {
    display: none;
    font-family: $arial;
    color: green;
    font-weight: bold;
    font-size: 20px;
}